<template>
    <div id="app">
        <el-container style="height:100%;">
            <el-header>
                <h1 style="float: left;">医院挂号系统</h1>
            </el-header>
            <el-container>
                <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
                    <el-menu router>
                        <el-submenu index="1" class="el-menu-vertical-demo">
                            <template slot="title"><i class="el-icon-menu"></i><span>挂号</span></template>
                            <el-menu-item index="1-1" :route="{path:'/registerOperation'}">患者挂号</el-menu-item>
                            <el-menu-item index="1-2" :route="{path:'/unsubscribeOperation'}">患者退号</el-menu-item>
                        </el-submenu>
                        <el-menu-item index="2" :route="{path:'/receptionOperation'}"><i class="el-icon-menu"></i><span slot="title">医师接诊</span></el-menu-item>
                        <el-menu-item index="3" :route="{path:'/testOperation'}"><i class="el-icon-menu"></i><span slot="title">体检项接诊</span></el-menu-item>
                        <el-menu-item index="4" :route="{path:'/chargeOperation'}"><i class="el-icon-menu"></i><span slot="title">划价/收费</span></el-menu-item>
                        <el-menu-item index="5" :route="{path:'/pharmacyOperation'}"><i class="el-icon-menu"></i><span slot="title">药房</span></el-menu-item>
                        <el-submenu index="6">
                            <template slot="title"><i class="el-icon-setting"></i><span>系统设置</span></template>
                            <el-menu-item index="6-1" :route="{path:'/userManager'}">用户管理</el-menu-item>
                            <el-menu-item index="6-2" :route="{path:'/departmentManager'}">科室管理</el-menu-item>
                            <el-menu-item index="6-3" :route="{path:'/titleManager'}">职称管理</el-menu-item>
                            <el-menu-item index="6-4" :route="{path:'/jurisdictionManager'}">权限管理</el-menu-item>
                            <el-menu-item index="6-5" :route="{path:'/medicineManager'}">药品管理</el-menu-item>
                            <el-menu-item index="6-6" :route="{path:'/testManager'}">体检项管理</el-menu-item>
                            <el-menu-item index="6-7" :route="{path:'/moduleManager'}">系统模块管理</el-menu-item>
                        </el-submenu>
                    </el-menu>
                </el-aside>
                <el-main>
                    <router-view/>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: 'app'
    }
</script>

<style src="../static/reset.css"></style>

<style>
    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
        height: 100%;
    }
</style>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .el-header, .el-footer {
        background-color: #B3C0D1;
        color: #333;
        text-align: center;
        line-height: 60px;
    }
  
    .el-aside {
        background-color: #D3DCE6;
        color: #333;
        text-align: center;
        line-height: 200px;
    }
  
    .el-main {
        background-color: #E9EEF3;
        color: #333;
        text-align: center;
        line-height: 160px;
    }
  
    body > .el-container {
        margin-bottom: 40px;
    }
  
    .el-container:nth-child(5) .el-aside, .el-container:nth-child(6) .el-aside {
        line-height: 260px;
    }
  
    .el-container:nth-child(7) .el-aside {
        line-height: 320px;
    }
</style>



